.add-notification {
  background-color: #003b46;
  padding: 20px;
  margin: 20px auto;
  border: 2px solid #66a5ad;
  border-radius: 8px;
  width: 90%;
  max-width: 1200px;
  max-height: 500px; /* フォーム全体の最大高さを設定 */
  color: #ffffff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  justify-content: space-between; /* 内容を上下で適切に配置 */
  box-sizing: border-box;
}

.add-notification h2 {
  font-size: 24px;
  text-align: center;
  margin-bottom: 20px;
  color: #66a5ad;
}

.add-notification form {
  display: flex;
  flex-direction: column;
  gap: 15px;
  flex-grow: 1; /* 上下のスペースを調整するため */
}

.add-notification label {
  font-size: 16px;
  margin-bottom: 5px;
  color: #ffffff;
}

.add-notification input[type="text"],
.add-notification textarea {
  border: 1px solid #66a5ad;
  border-radius: 6px;
  padding: 10px;
  font-size: 16px;
  width: 100%;
  background-color: #eaedf2;
  color: #003b46;
  outline: none;
  box-sizing: border-box;
}

.add-notification textarea {
  height: 120px;
  resize: none;
}

.add-notification input:focus,
.add-notification textarea:focus {
  border-color: #5bc8ac;
  box-shadow: 0 0 5px #5bc8ac;
}

.add-notification button[type="submit"] {
  background-color: #5bc8ac;
  color: #ffffff;
  font-size: 18px;
  padding: 10px 20px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  text-align: center;
  transition: background-color 0.3s ease;
  width: fit-content;
  align-self: center;
}

.add-notification button[type="submit"]:hover {
  background-color: #66a5ad;
}

@media screen and (max-width: 480px) {
  .add-notification {
    padding: 10px;
    width: 100%;
    max-height: 400px; /* モバイル用の高さを調整 */
  }

  .add-notification h2 {
    font-size: 20px;
  }

  .add-notification input[type="text"],
  .add-notification textarea {
    font-size: 14px;
  }

  .add-notification button[type="submit"] {
    font-size: 16px;
    padding: 8px 16px;
  }
}
