.message-input {
    align-items: center;
    gap: 10px;
    padding: 20px;
    border-top: 1px solid #ccc;
    position: sticky;
    bottom: 0;
    z-index: 100;
  }
  
  .message-input input {
    flex: 1;
    padding: 15px;
    border-radius: 25px;
    border: 1px solid #ccc;
  }
  
  .message-input button {
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 8px;
    padding: 12px;
    cursor: pointer;
    width: 80px;
    height: 45px;
    align-items: center;
    justify-content: center;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  